<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>物业信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>清洁管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入区域管理员名称" v-model="cleanManager">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加信息</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="70"></el-table-column>
        <el-table-column prop="cleanPlace" label="清洁区域" width="160"></el-table-column>
        <el-table-column prop="cleanManager" label="区域管理员" width="140"></el-table-column>
        <el-table-column prop="cleanPerson" label="清洁人员" width="140"></el-table-column>
        <el-table-column prop="cleanTime" label="清洁时间" width="120"></el-table-column>
        <el-table-column label="操作" min-width="250">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeCleanById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加清洁信息"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
        <el-form-item label="清洁区域" prop="cleanPlace">
          <el-input v-model="addForm.cleanPlace"></el-input>
        </el-form-item>
        <el-form-item label="区域管理员" prop="cleanManager">
          <el-input v-model="addForm.cleanManager"></el-input>
        </el-form-item>
        <el-form-item label="清洁人员" prop="cleanPerson">
          <el-input v-model="addForm.cleanPerson"></el-input>
        </el-form-item>
        <el-form-item label="清洁时间" prop="cleanTime">
          <el-input v-model="addForm.cleanTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addClean">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改清洁信息"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="110px" @close="editDialogClosed">
        <el-form-item label="清洁区域" prop="cleanPlace">
          <el-input v-model="editForm.cleanPlace"></el-input>
        </el-form-item>
        <el-form-item label="区域管理员" prop="cleanManager">
          <el-input v-model="editForm.cleanManager"></el-input>
        </el-form-item>
        <el-form-item label="清洁人员" prop="cleanPerson">
          <el-input v-model="editForm.cleanPerson"></el-input>
        </el-form-item>
        <el-form-item label="清洁时间" prop="cleanTime">
          <el-input v-model="editForm.cleanTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editClean">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm"  ref="editFormRef" label-width="110px" >
        <el-form-item label="清洁区域" prop="cleanPlace">
          <el-input v-model="readForm.cleanPlace" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="区域管理员" prop="cleanManager">
          <el-input v-model="readForm.cleanManager" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="清洁人员" prop="cleanPerson">
          <el-input v-model="readForm.cleanPerson" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="清洁时间" prop="cleanTime">
          <el-input v-model="readForm.cleanTime" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        name: "cleanManage",
      data() {
        return {
          pageSize: 8,
          currentPage: 1,
          tableData: {
            id:"",
            cleanPlace:"",
            cleanManager:"",
            cleanPerson:"",
            cleanTime:""
          },
          total: 0,
          cleanManager: "",
          addDialogVisible: false,
          addForm:{
            id:"",
            cleanPlace:"",
            cleanManager:"",
            cleanPerson:"",
            cleanTime:""
          },
          addFormRules:{
            cleanPlace:[
              {required: true, message: '清洁区域不可为空', trigger: 'blur'},
              {min:1,max:20,message: '字符长度在2~20个字符之间',trigger:'blur'}
            ],
            cleanManager:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            cleanPerson:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            cleanTime:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ]
          },
          editDialogVisible:false,
          editForm:{
            id:"",
            cleanPlace:"",
            cleanManager:"",
            cleanPerson:"",
            cleanTime:""
          },
          editFormRules:{
            cleanPlace:[
              {required: true, message: '清洁区域不可为空', trigger: 'blur'},
              {min:1,max:20,message: '字符长度在2~20个字符之间',trigger:'blur'}
            ],
            cleanManager:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            cleanPerson:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            cleanTime:[
              {required: true, message: '不可为空', trigger: 'blur'}
            ]
          },
          readDialogVisible:false,
          readForm:{
            id:"",
            cleanPlace:"",
            cleanManager:"",
            cleanPerson:"",
            cleanTime:""
          },
        }
      },
      created() {
        this.queryCleanList()
      },
      methods: {
        queryCleanList() {
          this.$http.get('clean/queryClean',
            {params: {currentPage: this.currentPage, pageSize: this.pageSize, cleanManager: this.cleanManager}})
            .then(res => {
              this.total = res.data.total;
              this.tableData = res.data.data;
            });
        },
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage
          this.queryCleanList()
        },
        handleSearch() {
          this.currentPage = 1
          this.queryCleanList()
        },
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        addClean(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            const {data:res}=await this.$http.post("clean/addClean",this.addForm)
            if(res.code!==200){
              this.$message.error("添加信息失败,请检查信息")
            }else {
              this.$message.success("添加成功")
              this.addDialogVisible=false
              this.queryCleanList()
            }
          })
        },
        showEditDialog(id){
          this.editDialogVisible =true
          this.$http.get('clean/getCleanById',{params:{id:id}})
            .then(res => {
              this.editForm = res.data;
            });
        },
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        editClean(){
          this.$refs.editFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("clean/editClean",this.editForm)
            if(res.code!==200){
              this.$message.error("修改信息失败,请检查信息")
            }else {
              this.$message.success("修改信息成功")
              this.editDialogVisible=false
              this.queryCleanList()
            }
          })
        },
        removeCleanById(id){
          this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.get('clean/removeCleanById',{params:{id:id}});
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.queryCleanList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        showReadDialog(id){
          this.readDialogVisible =true
          this.$http.get('clean/getCleanById',{params:{id:id}})
            .then(res => {
              this.readForm = res.data;
            });
        }
      }
    }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



